import React from 'react';
import cx from 'classnames';
import { useTheme } from '../utils';
import '@itwin/itwinui-css/css/dialog.css';
/**
 * Dialog title bar. Recommended to be used as a child of `Dialog`.
 * @example
 * <Dialog.TitleBar>My dialog title</Dialog.TitleBar>
 */
export const DialogTitleBarTitle = React.forwardRef((props, ref) => {
    const { children, className, ...rest } = props;
    useTheme();
    return (React.createElement("div", { className: cx('iui-dialog-title', className), ref: ref, ...rest }, children));
});
export default DialogTitleBarTitle;
